<template>
  <div class="wrapper">
    <div class="groups">
    <div class="group">
      <Container
        group-name="1"
        :get-child-payload="getChildPayload1"
        @drop="onDrop('items1', $event)"
        orientation="horizontal"
      >
        <Draggable v-for="item in items1" :key="item.id">
          <div class="draggable-item" :style="{backgroundPositionX: item.id[1]*11 + '%', backgroundPositionY: item.id[0]*33 +'%'}">
            
          </div>
        </Draggable>
      </Container>
    </div>
    <div class="group">
      <Container 
        group-name="1"
        :get-child-payload="getChildPayload2"
        @drop="onDrop('items2', $event)"
        orientation="horizontal"
      >
        <Draggable v-for="item in items2" :key="item.id">
          <div class="draggable-item"  :style="{backgroundPositionX: item.id[1]*11 + '%', backgroundPositionY: item.id[0]*33 +'%'}">
            
          </div>
        </Draggable>
      </Container>
    </div>
    <div class="group">
      <Container
        group-name="1"
        :get-child-payload="getChildPayload3"
        @drop="onDrop('items3', $event)"
        orientation="horizontal"
      >
        <Draggable v-for="item in items3" :key="item.id">
          <div class="draggable-item" :style="{backgroundPositionX: item.id[1]*11 + '%', backgroundPositionY: item.id[0]*33 +'%'}">
            
          </div>
        </Draggable>
      </Container>
    </div>
    <div class="group">
      <Container
        group-name="1"
        :get-child-payload="getChildPayload4"
        @drop="onDrop('items4', $event)"
        orientation="horizontal"
      >
        <Draggable v-for="item in items4" :key="item.id">
          <div class="draggable-item" :style="{backgroundPositionX: item.id[1]*11 + '%', backgroundPositionY: item.id[0]*33 +'%'}">
            
          </div>
        </Draggable>
      </Container>
    </div>
    </div>    
    <div>
      <img src="" alt="">
    </div>
  </div>

  <button @click="getGame()">asd</button>
  <div class="modal" v-if="winner">
    <p>Вы победили</p>
  </div>
  <div class="overlay" v-if="winner"></div>
  <router-view/>
</template>

<script>
    import { Container, Draggable } from "vue-dndrop";
import { applyDrag, generateItems,generateInnerItems } from "../utils/helpers.js";

function shuffleArray(array) {
        for (let i = array.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [array[i], array[j]] = [array[j], array[i]];
        }
}

export default {
  name: "Groups",

  components: { Container, Draggable },

  data() {
    return {
      items1: generateItems(10, (i) => ({
        id: "0" + i ,       
      })),
      items2: generateItems(10, (i) => ({
        id: "1" + i,       
      })),
      items3: generateItems(10, (i) => ({
        id: "2" + i,       
      })),
      items4: generateItems(10, (i) => ({
        id: "3" + i,        
      })),
      innerItems:generateInnerItems(40, (i) => ({
        id: i,       
      })),    
      winItems:[],  
      winner:false,
    };
  },

  methods: {
     async onDrop(collection, dropResult) {     
      await (this[collection] = applyDrag(this[collection], dropResult));
      await (this.winItems = [...this.items1, ...this.items2, ...this.items3, ...this.items4]);
      this.win()
    },

    getChildPayload1(index) {
      return this.items1[index];
    },

    getChildPayload2(index) {
      return this.items2[index];
    },

    getChildPayload3(index) {
      return this.items3[index];
    },

    getChildPayload4(index) {
      return this.items4[index];
    },
    getGame(){
      shuffleArray(this.innerItems);
      for (let i = 0; i < 10; i++) {
        this.items1[i]= {id: this.innerItems[i]["id"] +""};        
      }
      for (let i = 10; i < 20; i++) {
        this.items2[i-10]= {id: this.innerItems[i]["id"] +""};        
      }
      for (let i = 20; i < 30; i++) {
        this.items3[i-20]= {id: this.innerItems[i]["id"] +""};        
      }
      for (let i = 30; i < 40; i++) {
        this.items4[i-30]= {id: this.innerItems[i]["id"] +""};        
      }      
    },
     win(){      
      let z =0;
      for (let i = 0; i < this.innerItems.length; i++) {
        if (this.winItems[i]["id"]==this.innerItems[i]["id"]) {
          z++;
        }            
      }
      if (z==this.innerItems.length) {
        this.winner = true
        return;
      }
      this.winner = false;
    }
         
  },
  beforeMount(){    
 },
};
</script>

